const initStale = {
    lang :"EN"
}

const rootReduce=(state = initStale ,action)=>{
    if(action.type=== "CHANGE_LANG_TO_AR"){
        return{
            lang:"AR"
        }
    }else if(action.type==="CHANGE_LANG_TO_EN"){
        return{
            lang:"EN"
        }
    }
    return {...state}

}
export default  rootReduce;